import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import '../components/PurchaseRequest.css'; 

const ViewForm = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [form, setForm] = useState(null);

  useEffect(() => {
    fetchForm();
    // eslint-disable-next-line
  }, []);

  const fetchForm = async () => {
    try {
      const response = await axios.get(`http://192.168.0.86:5000/api/forms`);
      const foundForm = response.data.find(f => f.id === parseInt(id));
      if (foundForm) {
        setForm(foundForm);
      } else {
        alert("");
      }
    } catch (error) {
      console.error(" Error fetching form:", error);
    }
  };

  const handleBack = () => {
    navigate('/');
  };

  const handleDownloadPDF = () => {
    window.open(`http://192.168.0.86:5000/api/forms/${id}/pdf`, '_blank');
  };

  if (!form) return <p> ...</p>;

  let detailsObj = {};
  try {
    detailsObj = JSON.parse(form.details);
  } catch (err) {
    console.error(" Error parsing details JSON:", err);
  }

  const {
    name,
    department,
    date,
    deliveryDate,
    items = [],
    remarks,
    vendorName,
    vendorAddress1,
    vendorAddress2,
    vendorZip,
    currency,
    terms,
    supervisorEmail,
    depManagerComment,
    gmComment,
    signRequester,
    dateRequester,
    signDepartmentManager,
    dateDepartmentManager,
    signGeneralManager,
    dateGeneralManager,
    subTotal,
    vat,
    grandTotal
  } = detailsObj;

  //  xx,xxx.xx
  const subTotalStr = (subTotal || 0).toLocaleString('en-US', { minimumFractionDigits: 2 });
  const vatStr = (vat || 0).toLocaleString('en-US', { minimumFractionDigits: 2 });
  const grandTotalStr = (grandTotal || 0).toLocaleString('en-US', { minimumFractionDigits: 2 });

  return (
    <div className="print-page">   
      <div className="form-container">
        <div className="form-header">
          <img
            src="https://img2.pic.in.th/pic/logo14821dedd19c2ad18.png"
            alt="Company Logo"
            className="company-logo"
          />
          <h1>Newly Weds Foods (Thailand) Limited</h1>
          <p>General Purchase Requisition</p>
        </div>

        <h2>Form Details: {form.form_name}</h2>

        <div id="purchaseRequestForm">
          {/* Row 1: Name, Dept, Date */}
          <div className="input-row">
            <label>Name:</label>
            <span>{name}</span>

            <label>Department:</label>
            <span>{department}</span>
          </div>

          {/* Row 2: Manager Email, Delivery Date */}
          <div className="input-row">
            <label>Manager Email:</label>
            <span>{supervisorEmail}</span>

            <div className="date-group">
              <label>Date:</label>
              <span>{date}</span>
            </div>

            <div className="date-group">
              <label>Delivery Date:</label>
              <span>{deliveryDate}</span>
            </div>
          </div>

          {/*  Items */}
          <table className="pr-table">
            <thead>
              <tr>
                <th>ITEM</th>
                <th>Description</th>
                <th>Unit</th>
                <th>Quantity</th>
                <th>Cost</th>
                <th>Amount</th>
              </tr>
            </thead>
            <tbody>
              {items.map((row, index) => (
                <tr key={index} className="item-row">
                  <td>{index + 1}</td>
                  <td>{row.description}</td>
                  <td>{row.unit}</td>
                  <td>{row.quantity}</td>
                  <td>{row.cost}</td>
                  <td>
                    {parseFloat(row.amount || 0).toLocaleString('en-US', {
                      minimumFractionDigits: 2
                    })}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>

          {/* Remarks */}
          <div className="input-row remarks-row">
            <label>Remarks:</label>
            <div>{remarks}</div>
          </div>

          {/* main-row => Vendor Info + Summary */}
          <div className="main-row">
            <div className="left-col vendor-section">
              <h3>Vendor Info</h3>
              <div className="vendor-row">
                <label>Vendor Name:</label>
                <span>{vendorName}</span>
              </div>
              <div className="vendor-row">
                <label>Address1:</label>
                <span>{vendorAddress1}</span>
              </div>
              <div className="vendor-row">
                <label>Address2:</label>
                <span>{vendorAddress2}</span>
              </div>
              <div className="vendor-row">
                <label>Zip Code:</label>
                <span>{vendorZip}</span>
              </div>
            </div>

            <div className="right-col summary-section">
              <div className="input-row">
                <label>Currency:</label>
                <span>{currency}</span>

                <label>Terms:</label>
                <span>{terms}</span>
              </div>

              <div className="summary compact-summary">
                <p style={{ margin: '3px 0' }}>
                  Sub Total: {subTotalStr} {currency}
                </p>
                <p style={{ margin: '3px 0' }}>
                  VAT (7%): {vatStr} {currency}
                </p>
                <h3 style={{ margin: '5px 0' }}>
                  Grand Total: {grandTotalStr} {currency}
                </h3>
              </div>
            </div>
          </div>

          <div className="comment-row">
            <div className="comment-box">
              <h3>Comment by Department Manager</h3>
              <div>{depManagerComment}</div>
            </div>
            <div className="comment-box">
              <h3>Comment by General Manager</h3>
              <div>{gmComment}</div>
            </div>
          </div>

          <table className="sign-table">
            <thead>
              <tr>
                <th>Requester</th>
                <th>Department Manager</th>
                <th>General Manager</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>
                  <div className="sign-row">
                    <label>Sign:</label>
                    <span>{signRequester}</span>
                  </div>
                  <div className="sign-row">
                    <label>Date:</label>
                    <span>{dateRequester}</span>
                  </div>
                </td>
                <td>
                  <div className="sign-row">
                    <label>Sign:</label>
                    <span>{signDepartmentManager}</span>
                  </div>
                  <div className="sign-row">
                    <label>Date:</label>
                    <span>{dateDepartmentManager}</span>
                  </div>
                </td>
                <td>
                  <div className="sign-row">
                    <label>Sign:</label>
                    <span>{signGeneralManager}</span>
                  </div>
                  <div className="sign-row">
                    <label>Date:</label>
                    <span>{dateGeneralManager}</span>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>

          <div style={{ marginTop: '20px' }}>
            <button className="download-pdf no-print" onClick={handleDownloadPDF}> Download PDF</button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ViewForm;
