import React, { useState } from 'react';
import axios from 'axios';
import '../components/PurchaseRequest.css';

const PurchaseRequestForm = () => {
  const [formData, setFormData] = useState({
    name: '',
    department: '',
    date: new Date().toISOString().split('T')[0],
    deliveryDate: '',
    items: [
      { description: '', unit: '', quantity: '', cost: '', amount: '' },
      { description: '', unit: '', quantity: '', cost: '', amount: '' },
    ],
    remarks: '',
    vendorName: '',
    vendorAddress1: '',
    vendorAddress2: '',
    vendorZip: '',
    currency: 'THB',
    terms: '7 Days',
    supervisorEmail: '',
    depManagerComment: '',
    gmComment: '',
    signRequester: '',
    dateRequester: '',
    signDepartmentManager: '',
    dateDepartmentManager: '',
    signGeneralManager: '',
    dateGeneralManager: ''
  });

  //  state  insertedId 
  const [insertedId, setInsertedId] = useState(null);
  const [isSendingEmail, setIsSendingEmail] = useState(false);


  const departments = [
    'Group Management', 'Customer Service', 'Finance and Accounting',
    'Information Communication and Technology', 'Human Resources',
    'Occupational Health and Safety', 'Operation and Production',
    'Production Planning and Control', 'Purchasing', 'Maintenance and Engineering',
    'Quality Control', 'Warehouse and Distribution'
  ];

  const currencies = ['AUD', 'JPY', 'EUR', 'THB', 'USD', 'PHP'];
  const termsList = ['7 Days', '15 Days', '30 Days', '45 Days', '60 Days', 'CASH', 'Bank Transfer', 'TT Advance'];

  // handleCellChange =>  onChange  <input> 
  const handleCellChange = (e, rowIndex, field) => {
    const newItems = [...formData.items];
    const value = e.target.value;
    newItems[rowIndex][field] = value;

    if (['unit', 'quantity', 'cost'].includes(field)) {
      const u   = parseFloat(newItems[rowIndex].unit) || 0;
      const qty = parseFloat(newItems[rowIndex].quantity) || 0;
      const cst = parseFloat(newItems[rowIndex].cost) || 0;
      newItems[rowIndex].amount = (u * qty * cst).toFixed(2); 
    }

    setFormData({ ...formData, items: newItems });
  };

  //  Subtotal, VAT, Grand Total
  const subTotalNum = formData.items.reduce((acc, item) => {
    const amt = parseFloat(item.amount) || 0;
    return acc + amt;
  }, 0);
  const vatNum = subTotalNum * 0.07;
  const grandTotalNum = subTotalNum + vatNum;

  const subTotalStr = subTotalNum.toLocaleString('en-US', { minimumFractionDigits: 2 });
  const vatStr = vatNum.toLocaleString('en-US', { minimumFractionDigits: 2 });
  const grandTotalStr = grandTotalNum.toLocaleString('en-US', { minimumFractionDigits: 2 });

  // / rows
  const addItem = () => {
    setFormData(prev => ({
      ...prev,
      items: [...prev.items, { description: '', unit: '', quantity: '', cost: '', amount: '' }]
    }));
  };

  const handleDeleteItem = (rowIndex) => {
    setFormData(prev => {
      const newItems = [...prev.items];
      newItems.splice(rowIndex, 1);
      return { ...prev, items: newItems };
    });
  };

  // Handle change 
  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  // Submit 
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post('http://192.168.0.86:5000/api/forms', {
        form_name: "Purchase Request",
        user_name: formData.name,
        department: formData.department,
        details: JSON.stringify({
          ...formData,
          subTotal: subTotalNum,
          vat: vatNum,
          grandTotal: grandTotalNum
        }),
        supervisorEmail: formData.supervisorEmail
      });
      alert(' Form submitted successfully!');

      //  insertedId 
      const { insertedId } = response.data;
      setInsertedId(insertedId);
    } catch (error) {
      console.error(' Error:', error);
      alert(' Error submitting form');
    }
  };

  //  Send Email ( submit)
  const handleSendEmail = async () => {
    //  
    if (!formData.supervisorEmail) {
      alert(' Manager Email ');
      return;
    }
    if (!insertedId) {
      alert(' ');
      return;
    }
  
    try {
      setIsSendingEmail(true); // 
      await axios.post('http://192.168.0.86:5000/api/forms/pdf-email', {
        id: insertedId,
        email: formData.supervisorEmail
      });
      alert(' Email sent successfully!');
    } catch (error) {
      console.error(' Error sending email:', error);
      alert(' Error sending email');
    } finally {
      setIsSendingEmail(false); //   success  error
    }
  };
  

  // Print
  const handlePrint = () => {
    window.print();
  };
    
  return (
    <div className="print-page">
      <div className="form-container">
        <div className="form-header">
          <img
            src="https://img2.pic.in.th/pic/logo14821dedd19c2ad18.png"
            alt="Company Logo"
            className="company-logo"
          />
          <h1>Newly Weds Foods (Thailand) Limited</h1>
          <p>General Purchase Requisition</p>
        </div>

        <form onSubmit={handleSubmit} id="purchaseRequestForm">
          {/* Row 1: Name, Dept, Date */}
          <div className="input-row">
            <label>Name:</label>
            <input
              type="text"
              name="name"
              value={formData.name}
              onChange={handleChange}
              required
            />

            <label>Department:</label>
            <select
              name="department"
              value={formData.department}
              onChange={handleChange}
              required
            >
              <option value="">Select Department</option>
              {departments.map(dep => (
                <option key={dep} value={dep}>{dep}</option>
              ))}
            </select>
          </div>

          {/* Row 2: Manager Email, Delivery Date */}
          <div className="input-row">
            <label>Manager Email:</label>
            {/*  required  */}
            <input
              type="email"
              name="supervisorEmail"
              value={formData.supervisorEmail}
              onChange={handleChange}
            />

            <div className="date-group">
              <label>Date:</label>
              <input
                type="date"
                name="date"
                value={formData.date}
                onChange={handleChange}
                required
              />
            </div>

            <div className="date-group">
              <label>Delivery Date:</label>
              <input
                type="date"
                name="deliveryDate"
                value={formData.deliveryDate}
                onChange={handleChange}
              />
            </div>
          </div>

          {/*  Items (6 ) +  Absolute Position  Amount */}
          <table className="pr-table">
            <thead>
              <tr>
                <th>ITEM</th>
                <th>Description</th>
                <th>Unit</th>
                <th>Quantity</th>
                <th>Cost</th>
                <th>Amount</th>
              </tr>
            </thead>
            <tbody>
              {formData.items.map((row, index) => (
                <tr key={index} className="item-row">
                  <td>{index + 1}</td>
                  <td>
                    <input
                      type="text"
                      value={row.description}
                      onChange={(e) => handleCellChange(e, index, 'description')}
                    />
                  </td>
                  <td>
                    <input
                      type="number"
                      value={row.unit}
                      onChange={(e) => handleCellChange(e, index, 'unit')}
                    />
                  </td>
                  <td>
                    <input
                      type="number"
                      value={row.quantity}
                      onChange={(e) => handleCellChange(e, index, 'quantity')}
                    />
                  </td>
                  <td>
                    <input
                      type="number"
                      value={row.cost}
                      onChange={(e) => handleCellChange(e, index, 'cost')}
                    />
                  </td>
                  {/*  Amount +   */}
                  <td className="amount-cell">
                    {parseFloat(row.amount || 0).toLocaleString('en-US', { minimumFractionDigits: 2 })}
                    <button
                      type="button"
                      className="delete-btn outside"
                      onClick={() => handleDeleteItem(index)}
                    >
                      
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
          <button
            type="button"
            className="button"
            onClick={addItem}
          >
             Add Item
          </button>

          <p></p>

          {/* Remarks */}
          <div className="input-row remarks-row">
            <label>Remarks:</label>
            <textarea
              name="remarks"
              rows="2"
              value={formData.remarks}
              onChange={handleChange}
            />
          </div>

          {/* main-row => : Vendor Info / : Currency, Terms, Subtotal */}
          <div className="main-row">
            <div className="left-col vendor-section">
              <h3>Vendor Info</h3>
              <div className="vendor-row">
                <label>Vendor Name:</label>
                <input
                  type="text"
                  name="vendorName"
                  value={formData.vendorName}
                  onChange={handleChange}
                />
              </div>
              <div className="vendor-row">
                <label>Address1:</label>
                <input
                  type="text"
                  name="vendorAddress1"
                  value={formData.vendorAddress1}
                  onChange={handleChange}
                />
              </div>
              <div className="vendor-row">
                <label>Address2:</label>
                <input
                  type="text"
                  name="vendorAddress2"
                  value={formData.vendorAddress2}
                  onChange={handleChange}
                />
              </div>
              <div className="vendor-row">
                <label>Zip Code:</label>
                <input
                  type="text"
                  name="vendorZip"
                  value={formData.vendorZip}
                  onChange={handleChange}
                />
              </div>
            </div>
            <div className="right-col summary-section">
              <div className="input-row">
                <label>Currency:</label>
                <select
                  name="currency"
                  value={formData.currency}
                  onChange={handleChange}
                >
                  {currencies.map(curr => (
                    <option key={curr} value={curr}>{curr}</option>
                  ))}
                </select>

                <label>Terms:</label>
                <select
                  name="terms"
                  value={formData.terms}
                  onChange={handleChange}
                >
                  {termsList.map(term => (
                    <option key={term} value={term}>{term}</option>
                  ))}
                </select>
              </div>

              <div className="summary compact-summary">
                <p style={{ margin: '3px 0' }}>
                  Sub Total: {subTotalStr} {formData.currency}
                </p>
                <p style={{ margin: '3px 0' }}>
                  VAT (7%): {vatStr} {formData.currency}
                </p>
                <h3 style={{ margin: '5px 0' }}>
                  Grand Total: {grandTotalStr} {formData.currency}
                </h3>
              </div>
            </div>
          </div>

          <div className="comment-row">
            <div className="comment-box">
              <h3>Comment by Department Manager</h3>
              <textarea
                name="depManagerComment"
                rows="4"
                value={formData.depManagerComment}
                onChange={handleChange}
              />
            </div>
            <div className="comment-box">
              <h3>Comment by General Manager</h3>
              <textarea
                name="gmComment"
                rows="4"
                value={formData.gmComment}
                onChange={handleChange}
              />
            </div>
          </div>

          <table className="sign-table">
            <thead>
              <tr>
                <th>Requester</th>
                <th>Department Manager</th>
                <th>General Manager</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>
                  <div className="sign-row">
                    <label>Sign:</label>
                    <input
                      type="text"
                      name="signRequester"
                      value={formData.signRequester}
                      onChange={handleChange}
                    />
                  </div>
                  <div className="sign-row">
                    <label>Date:</label>
                    <input
                      type="text"
                      name="dateRequester"
                      value={formData.dateRequester}
                      onChange={handleChange}
                    />
                  </div>
                </td>
                <td>
                  <div className="sign-row">
                    <label>Sign:</label>
                    <input
                      type="text"
                      name="signDepartmentManager"
                      value={formData.signDepartmentManager}
                      onChange={handleChange}
                    />
                  </div>
                  <div className="sign-row">
                    <label>Date:</label>
                    <input
                      type="text"
                      name="dateDepartmentManager"
                      value={formData.dateDepartmentManager}
                      onChange={handleChange}
                    />
                  </div>
                </td>
                <td>
                  <div className="sign-row">
                    <label>Sign:</label>
                    <input
                      type="text"
                      name="signGeneralManager"
                      value={formData.signGeneralManager}
                      onChange={handleChange}
                    />
                  </div>
                  <div className="sign-row">
                    <label>Date:</label>
                    <input
                      type="text"
                      name="dateGeneralManager"
                      value={formData.dateGeneralManager}
                      onChange={handleChange}
                    />
                  </div>
                </td>
              </tr>
            </tbody>
          </table>

          <div className="btn-row">
            {/*  Submit Form */}
            <button type="submit" className="button submit-btn">
               Submit Form
            </button>

            {/*  Send Email -  submit,  email  submit  */}
            <button
              type="button"
              className="button"
              onClick={handleSendEmail}
              disabled={!formData.supervisorEmail || !insertedId|| isSendingEmail}
            >
               {isSendingEmail ? 'Sending...' : 'Send Email'}
            </button>

            <button
              type="button"
              className="button print-btn"
              onClick={handlePrint}
            >
               Print
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default PurchaseRequestForm;
